import React, { useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import { set, useForm } from "react-hook-form";

import pageStyles from "./index.module.scss";

// needed assets
import profilePic from "assets/images/profilePic.png";
import { useDispatch, useSelector } from "react-redux";
import { editUserProfileData, getUserProfileData } from "store/auth/slice";
import { convertDateValue } from "helpers/functions";
import { v4 } from "uuid";
import {
  getDownloadURL,
  ref,
  uploadBytes,
  uploadBytesResumable,
} from "firebase/storage";
import { storage } from "firebaseConfig/firebaseConfig";
import { toast } from "react-toastify";
const ProfileInfoForm = () => {
  const dispatch = useDispatch();
  const [selectedFile, setSelectedFile] = useState(null);
  const [imageUpload, setImageUpload] = useState("");
  const [progress, setProgress] = useState(false);

  const {
    register,
    handleSubmit,
    setValue,
    formState: { errors },
  } = useForm({});
  const onSubmit = async (data) => {
    data["notification"] = true;
    data["date_of_birth"] = convertDateValue(data["date_of_birth"]);
    data["picture"] = imageUpload;
    dispatch(editUserProfileData({ data }));
  };

  const { user } = useSelector((state) => state.authentication) || {};
  const { email, picture, last_name, first_name, date_of_birth } = user || {};

  const defaultValues = {
    email,
    picture,
    last_name,
    first_name,
    date_of_birth,
  };
  useEffect(() => {
    if (user.date_of_birth) {
      for (const key in defaultValues) {
        if (Object.hasOwnProperty.call(defaultValues, key)) {
          const value = defaultValues[key];
          if (key === "date_of_birth") {
            setValue(key, convertDateValue(value));
          } else {
            setValue(key, value);
          }
        }
      }
    }
  }, [setValue, user]);

  const handleImageUpload = async (e) => {
    if (!e.target.files || e.target.files.length === 0) {
      return;
    }
    const file = e.target.files[0];
    setSelectedFile((prev) => file);
    const storageRef = ref(storage, `images/${file?.name}`);

    const uploadTask = uploadBytesResumable(storageRef, file);
    uploadTask.on(
      "state_changed",
      (snapshot) => {
        const percent = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        // update progress
        setProgress(true);
      },
      (err) => {
        toast.error("error uploading image");
        setSelectedFile(null);
        setProgress(false);
      },
      () => {
        // download url
        getDownloadURL(uploadTask.snapshot.ref).then((url) => {
          console.log(url);
          setImageUpload(url);
          setProgress(false);
        });
      }
    );
  };

  const errorMessage = "هذا الحقل مطلوب";

  return (
    <div>
      <form action="#" method="get" onSubmit={handleSubmit(onSubmit)}>
        <Row style={{ rowGap: "1rem" }}>
          <Col
            xs={12}
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <label htmlFor="picture" className={pageStyles.profilePic}>
              <img src={picture || profilePic} alt="profilePic" />
              <input
                id="picture"
                name="picture"
                type="file"
                {...register("picture", {
                  required: false,
                  onChange: handleImageUpload,
                  type: "mix",
                })}
              />
            </label>

            {errors?.picture && (
              <p className="error-message">{errors?.picture?.message}</p>
            )}
          </Col>
          <Col xs={12}>
            <label htmlFor="first_name">
              <p>الاسم</p>
              <input
                id="first_name"
                name="first_name"
                type={"text"}
                {...register("first_name", { required: errorMessage })}
              />
            </label>

            {errors?.name && (
              <p className="error-message">{errors?.name?.message}</p>
            )}
          </Col>
          <Col xs={12}>
            <label htmlFor="last_name">
              <p>اسم العائلة </p>
              <input
                type="text"
                id="last_name"
                name="last_name"
                {...register("last_name", { required: errorMessage })}
              />
            </label>

            {errors?.name && (
              <p className="error-message">{errors?.name?.message}</p>
            )}
          </Col>
          <Col xs={12}>
            <label htmlFor="email">
              <p>البريد الإلكتروني</p>
              <input
                type="email"
                id="email"
                name="email"
                {...register("email", { required: errorMessage })}
              />
            </label>

            {errors?.name && (
              <p className="error-message">{errors?.name?.message}</p>
            )}
          </Col>
          <Col xs={12}>
            <label htmlFor="date_of_birth">
              <p>تاريخ الميلاد</p>
              <input
                type="date"
                id="date_of_birth"
                name="date_of_birth"
                {...register("date_of_birth", {
                  required: errorMessage,
                  valueAsDate: true,
                })}
              />
            </label>

            {errors?.name && (
              <p className="error-message">{errors?.name?.message}</p>
            )}
          </Col>

          <Col>
            <input
              disabled={!!progress}
              type="submit"
              value={progress ? `.....uploading` : "save"}
            />
          </Col>
        </Row>
      </form>
    </div>
  );
};

export default ProfileInfoForm;
