import React, { useState } from "react";
import { Accordion, Col, Container, Row } from "react-bootstrap";
import CustomToggle from "components/shared/CustomToggle";

// needed styles
import pageStyles from "./index.module.scss";
import { v4 } from "uuid";
import ProfileInfoForm from "./ProfileInfoForm";
import PasswordForm from "./passwordForm";

const profileKey = v4();
const passwordKey = v4();

const ProfilePageComponent = () => {
  const [activeKey, setActiveKey] = useState(profileKey);

  return (
    <div>
      <Container className={pageStyles.body}>
        <Row style={{ justifyContent: "center" }}>
          <Col xs={12} md={8} className={pageStyles.contianer}>
            <Accordion
              defaultActiveKey={activeKey}
              className={pageStyles.accordionForms}
            >
              <Row className={`${pageStyles.TogglesRow}`}>
                <Col xs={12} md={6} className={pageStyles.colCell}>
                  <CustomToggle
                    eventKey={profileKey}
                    defaultClass={pageStyles.toggle}
                    children={<>بيانات الملف الشخصي</>}
                    alwaysOpen={true}
                    activeKey={activeKey}
                    openClass={pageStyles.activeToggle}
                    setter={() => setActiveKey(profileKey)}
                  />
                </Col>
                {/* <Col xs={12} md={6} className={pageStyles.colCell}>
                  <CustomToggle
                    eventKey={passwordKey}
                    openClass={pageStyles.activeToggle}
                    defaultClass={pageStyles.toggle}
                    children={<>تعديل كلمة السر</>}
                    activeKey={activeKey}
                    setter={() => setActiveKey(passwordKey)}
                  />
                </Col> */}
              </Row>
              <Accordion.Item
                eventKey={profileKey}
                className={pageStyles.AccordionItem}
              >
                <Accordion.Body>
                  <ProfileInfoForm />
                </Accordion.Body>
              </Accordion.Item>
              {/* 
              <Accordion.Item
                eventKey={passwordKey}
                className={pageStyles.AccordionItem}
              >
                <Accordion.Body>
                  <PasswordForm />
                </Accordion.Body>
              </Accordion.Item> */}
            </Accordion>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default ProfilePageComponent;
